'use client';

import { Banknote, CreditCard, Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogFooter,
    DialogDescription
} from '@/components/ui/dialog';

interface PaymentMethod { id: number; nama_metode: string; }

interface POSCheckoutDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    totalAmount: number;
    payments: PaymentMethod[];
    selectedPayment: number | null;
    setSelectedPayment: (id: number) => void;
    customerName: string;
    setCustomerName: (name: string) => void;
    cashAmount: string;
    setCashAmount: (amount: string) => void;
    onProcess: () => void;
    isProcessing: boolean;
}

export default function POSCheckoutDialog({
    isOpen, onOpenChange, totalAmount, payments,
    selectedPayment, setSelectedPayment,
    customerName, setCustomerName,
    cashAmount, setCashAmount,
    onProcess, isProcessing
}: POSCheckoutDialogProps) {

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);
        
    const bayar = parseFloat(cashAmount) || 0;
    const kembalian = Math.max(0, bayar - totalAmount);
    const isCashEnough = bayar >= totalAmount;

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle className="text-center text-xl">Pembayaran</DialogTitle>
                    <DialogDescription className="text-center">
                        Total Tagihan: <span className="font-bold text-gray-900 text-lg">{formatRupiah(totalAmount)}</span>
                    </DialogDescription>
                </DialogHeader>
                
                <div className="grid gap-4 py-4">
                    <div className="space-y-2">
                        <Label>Nama Pelanggan (Opsional)</Label>
                        <Input 
                            placeholder="Cth: Pak Budi" 
                            value={customerName}
                            onChange={e => setCustomerName(e.target.value)}
                        />
                    </div>

                    <div className="space-y-2">
                        <Label>Metode Pembayaran</Label>
                        <div className="grid grid-cols-3 gap-2">
                            {payments.map(method => (
                                <div 
                                    key={method.id}
                                    onClick={() => setSelectedPayment(method.id)}
                                    className={`cursor-pointer rounded-lg border p-3 flex flex-col items-center justify-center gap-2 transition-all ${
                                        selectedPayment === method.id 
                                            ? 'border-[#D93F21] bg-red-50 text-[#D93F21]' 
                                            : 'border-gray-200 hover:border-gray-300'
                                    }`}
                                >
                                    {method.nama_metode.toLowerCase().includes('cash') ? (
                                        <Banknote className="h-5 w-5" />
                                    ) : (
                                        <CreditCard className="h-5 w-5" />
                                    )}
                                    <span className="text-xs font-medium text-center">{method.nama_metode}</span>
                                </div>
                            ))}
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label>Uang Diterima</Label>
                        <Input 
                            type="number" 
                            placeholder="Masukkan jumlah uang..." 
                            value={cashAmount}
                            onChange={e => setCashAmount(e.target.value)}
                            className="text-lg font-mono"
                        />
                        <div className="flex gap-2 mt-2 overflow-x-auto pb-1">
                            {[totalAmount, 20000, 50000, 100000].map(amt => (
                                amt >= totalAmount && (
                                    <Button 
                                        key={amt} 
                                        type="button" 
                                        variant="outline" 
                                        size="sm" 
                                        className="text-xs"
                                        onClick={() => setCashAmount(amt.toString())}
                                    >
                                        {formatRupiah(amt)}
                                    </Button>
                                )
                            ))}
                        </div>
                    </div>

                    <div className="bg-gray-100 p-3 rounded-lg flex justify-between items-center">
                        <span className="text-sm font-medium text-gray-600">Kembalian</span>
                        <span className={`text-lg font-bold ${isCashEnough ? 'text-green-600' : 'text-gray-400'}`}>
                            {formatRupiah(kembalian)}
                        </span>
                    </div>
                </div>

                <DialogFooter>
                    <Button 
                        className="w-full bg-[#D93F21] hover:bg-[#b9351b] py-6 text-lg" 
                        onClick={onProcess}
                        disabled={isProcessing}
                    >
                        {isProcessing ? <Loader2 className="h-5 w-5 animate-spin" /> : "Proses Transaksi"}
                    </Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}