'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';

import POSFilterBar from '@/components/karyawan/pos/POSFilterBar';
import POSProductGrid from '@/components/karyawan/pos/POSProductGrid';
import POSCart from '@/components/karyawan/pos/POSCart';
import POSCheckoutDialog from '@/components/karyawan/pos/POSCheckoutDialog';
import POSSuccessDialog from '@/components/karyawan/pos/POSSuccessDialog';

import { ProdukResto, Kategori, PaymentMethod } from '@/types';

interface CartItem extends Omit<ProdukResto, 'harga_jual'> {
    harga_jual: number;
    qty: number;
}

export default function POSPage() {
   
    const [products, setProducts] = useState<ProdukResto[]>([]);
    const [categories, setCategories] = useState<Kategori[]>([]);
    const [payments, setPayments] = useState<PaymentMethod[]>([]);
    const [loading, setLoading] = useState(true);

    const [searchQuery, setSearchQuery] = useState('');
    const [selectedCategory, setSelectedCategory] = useState<string>('all');
    const [cart, setCart] = useState<CartItem[]>([]);

    const [isCheckoutOpen, setIsCheckoutOpen] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);
    const [selectedPayment, setSelectedPayment] = useState<number | null>(null);
    const [customerName, setCustomerName] = useState('');
    const [cashAmount, setCashAmount] = useState('');
    const [lastTransaction, setLastTransaction] = useState<any>(null);

    useEffect(() => {
        const initData = async () => {
            try {
                const [resProd, resKat, resPay] = await Promise.all([
                    axios.get('/api/produkRestos'),
                    axios.get('/api/kategoris'),
                    axios.get('/api/paymentMethods'),
                ]);
                setProducts(resProd.data);
                setCategories(resKat.data);
                setPayments(resPay.data.filter((p: PaymentMethod) => p.is_aktif));
            } catch (error) {
                console.error(error);
                toast.error("Gagal memuat data POS.");
            } finally {
                setLoading(false);
            }
        };
        initData();
    }, []);

    const filteredProducts = products.filter(item => {
        const matchName = item.nama_produk.toLowerCase().includes(searchQuery.toLowerCase());
        const matchCat = selectedCategory === 'all' || item.kategori_id.toString() === selectedCategory;
        return matchName && matchCat && item.is_tersedia; 
    });

    const addToCart = (product: ProdukResto) => {
        setCart(prev => {
            const existing = prev.find(item => item.id === product.id);
            if (existing) {
                return prev.map(item => item.id === product.id ? { ...item, qty: item.qty + 1 } : item);
            }
            return [...prev, { ...product, harga_jual: Number(product.harga_jual), qty: 1 }];
        });
    };

    const removeFromCart = (id: number) => setCart(prev => prev.filter(item => item.id !== id));

    const updateQty = (id: number, delta: number) => {
        setCart(prev => prev.map(item => {
            if (item.id === id) {
                const newQty = item.qty + delta;
                return newQty > 0 ? { ...item, qty: newQty } : item;
            }
            return item;
        }));
    };

    const totalCartPrice = cart.reduce((sum, item) => sum + (item.harga_jual * item.qty), 0);

    const handleCheckout = async () => {
        if (!selectedPayment) { toast.error("Pilih metode pembayaran!"); return; }
        
        const bayar = parseFloat(cashAmount);
        if (isNaN(bayar) || bayar < totalCartPrice) { toast.error("Uang pembayaran kurang!"); return; }

        setIsProcessing(true);
        try {
            const payload = {
                payment_method_id: selectedPayment,
                total_bayar: bayar,
                nama_pelanggan: customerName || 'Umum',
                items: cart.map(item => ({ produk_resto_id: item.id, jumlah: item.qty }))
            };
            const response = await axios.post('/api/transaksiRestos', payload);
            
            setLastTransaction(response.data);
            setCart([]);
            setCashAmount('');
            setCustomerName('');
            setSelectedPayment(null);
            setIsCheckoutOpen(false);
            
            toast.success("Transaksi Berhasil!");
        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Transaksi Gagal.");
        } finally {
            setIsProcessing(false);
        }
    };

    return (
        <div className="flex flex-col lg:flex-row h-[calc(100vh-80px)] gap-4">
            
            <div className="flex-1 flex flex-col gap-4 overflow-hidden">
                <POSFilterBar 
                    categories={categories}
                    selectedCategory={selectedCategory}
                    setSelectedCategory={setSelectedCategory}
                    searchQuery={searchQuery}
                    setSearchQuery={setSearchQuery}
                />
                <POSProductGrid 
                    loading={loading}
                    products={filteredProducts}
                    onAddToCart={addToCart}
                />
            </div>

            <POSCart 
                cart={cart} 
                updateQty={updateQty}
                removeFromCart={removeFromCart}
                onCheckout={() => setIsCheckoutOpen(true)}
            />

            {/* MODAL-MODAL */}
            <POSCheckoutDialog 
                isOpen={isCheckoutOpen}
                onOpenChange={setIsCheckoutOpen}
                totalAmount={totalCartPrice}
                payments={payments}
                selectedPayment={selectedPayment}
                setSelectedPayment={setSelectedPayment}
                customerName={customerName}
                setCustomerName={setCustomerName}
                cashAmount={cashAmount}
                setCashAmount={setCashAmount}
                onProcess={handleCheckout}
                isProcessing={isProcessing}
            />

            <POSSuccessDialog 
                transaction={lastTransaction}
                onClose={() => setLastTransaction(null)}
            />
        </div>
    );
}