'use client';

import { Search } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';

interface Kategori { id: number; nama_kategori: string; }

interface POSFilterBarProps {
    categories: Kategori[];
    selectedCategory: string;
    setSelectedCategory: (id: string) => void;
    searchQuery: string;
    setSearchQuery: (query: string) => void;
}

export default function POSFilterBar({
    categories,
    selectedCategory,
    setSelectedCategory,
    searchQuery,
    setSearchQuery
}: POSFilterBarProps) {
    return (
        <div className="bg-white p-4 rounded-xl shadow-sm space-y-4 flex-shrink-0">
            <div className="flex gap-2 overflow-x-auto pb-2 custom-scrollbar">
                <Button 
                    variant={selectedCategory === 'all' ? 'default' : 'outline'}
                    onClick={() => setSelectedCategory('all')}
                    className="rounded-full text-xs h-8 whitespace-nowrap bg-[#D93F21] hover:bg-[#b9351b] text-white border-none"
                >
                    Semua
                </Button>
                {categories.map(cat => (
                    <Button
                        key={cat.id}
                        variant={selectedCategory === cat.id.toString() ? 'default' : 'outline'}
                        onClick={() => setSelectedCategory(cat.id.toString())}
                        className={`rounded-full text-xs h-8 whitespace-nowrap ${
                            selectedCategory === cat.id.toString() 
                                ? 'bg-gray-800 text-white' 
                                : 'hover:bg-gray-100'
                        }`}
                    >
                        {cat.nama_kategori}
                    </Button>
                ))}
            </div>
            <div className="relative">
                <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                <Input 
                    placeholder="Cari menu..." 
                    className="pl-9 bg-gray-50 border-none"
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                />
            </div>
        </div>
    );
}