'use client';

import Image from 'next/image';
import { Loader2, UtensilsCrossed } from 'lucide-react';
import { Card } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';
import { ProdukResto } from '@/types'; 

interface POSProductGridProps {
    loading: boolean;
    products: ProdukResto[];
    onAddToCart: (product: ProdukResto) => void;
}

export default function POSProductGrid({ loading, products, onAddToCart }: POSProductGridProps) {
    
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    return (
        <ScrollArea className="flex-1 bg-transparent rounded-md">
            <div className="pr-3 pb-4">
                {loading ? (
                    <div className="flex justify-center py-10">
                        <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
                    </div>
                ) : products.length === 0 ? (
                    <div className="text-center py-10 text-gray-500">
                        <UtensilsCrossed className="h-12 w-12 mx-auto mb-2 opacity-20" />
                        <p>Menu tidak ditemukan</p>
                    </div>
                ) : (
                    <div className="grid grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-4">
                        {products.map(product => (
                            <Card 
                                key={product.id} 
                                className="cursor-pointer hover:shadow-md transition-all border-0 shadow-sm group overflow-hidden"
                                onClick={() => onAddToCart(product)}
                            >
                                <div className="relative h-32 w-full bg-gray-100">
                                    {product.foto_url ? (
                                        <Image 
                                            src={product.foto_url} 
                                            alt={product.nama_produk} 
                                            fill 
                                            className="object-cover group-hover:scale-105 transition-transform duration-300"
                                            unoptimized
                                            onError={(e) => (e.target as HTMLElement).style.display = 'none'}
                                        />
                                    ) : (
                                        <div className="flex items-center justify-center h-full text-gray-300">No IMG</div>
                                    )}
                                    <Badge className="absolute top-2 left-2 bg-white/90 text-black text-[10px] hover:bg-white backdrop-blur-sm">
                                        {product.kategori?.nama_kategori}
                                    </Badge>
                                </div>
                                <div className="p-3">
                                    <h3 className="font-bold text-gray-800 text-sm truncate">{product.nama_produk}</h3>
                                    <p className="text-[#D93F21] font-bold text-sm mt-1">{formatRupiah(product.harga_jual)}</p>
                                </div>
                            </Card>
                        ))}
                    </div>
                )}
            </div>
        </ScrollArea>
    );
}